.swadist__blog-container_article{
    width:100%;
    height:100%;
    display: flex;
    flex-direction: column;
    background-color: var(--color-footer);
}

.swadist__blog-container_article-image{
    width: 100%;
    height: 100%;
    background: var(--color-footer);
}

.swadist__blog-container_article-image img{
    width: 100%;
    height: 100%;
}

.swadist__blog-container_article-content{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1rem 1.5rem;
    height:100%;
}

.swadist__blog-container_article-content p{
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 11.649px;
    line-height: 35px;
    color: #ffffff;
}

.swadist__blog-container_article-content h3 {
    font-family: var(--font-family);
        font-weight: 800;
        font-size: 25.11px;
        line-height: 30px;
        color: #ffffff;
        margin-bottom: 5rem;
        cursor: pointer;
}

.swadist__blog-container_article-content p:last-child {
    cursor: pointer;
}

@media screen and (max-width:550px){
    .swadist__blog-container_article-content h3{
        font-size: 16px;
        line-height: 25px;
    }
}
.swadist__brand{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.swadist__brand div{
    flex:1 1;
    max-width: 150px;
    min-width: 120px;
    margin: 1rem;

    display: flex;
    justify-content: center;
    align-items: center;
}
.swadist__cta{
    border: 1px solid white;
    color:white;
    display:flex;
    justify-content:space-between;
    align-items:center;
    flex-direction: row;
    padding: 2rem;
    margin: 4rem;
    border-radius: 1rem;
    background: var(--gradient-bar);
}

.swadist__cta-content p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 30px;
    color: #0c0c0c;
}

.swadist__cta-content h3{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 24px;
    line-height: 45px;
    color: #000000;
}

.swadist__cta-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}

.swadist__cta-btn button{
    background: #000000;
    border-radius: 40px;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 18px;
    line-height: 30px;
    color: #FFFFFF;
    padding: 0.5rem 1rem;
    border: none;
    outline: none;
    cursor: pointer;
    min-width: 150px;
}

